import { React, useEffect, useState } from 'react';
import PokemonMylist from '../components/PokemonMyList'

const MyList = () => {

    const [allPokemon, setAllPokemon] = useState([])
    const [refreshPokemon, setRefreshPokemon] = useState(false)

    const getAllPokemons = async () => {
        console.log("run")
        let listPokemon = localStorage.getItem('ListPokemon');
        const listPokemonJSON = JSON.parse(listPokemon)

        if (listPokemon) {
            listPokemonJSON.forEach( async (pokemon) => {
                setAllPokemon(currentList => [...currentList, pokemon])
            })
        }

    }

  useEffect(() => {
      getAllPokemons()
  }, [])
  
  useEffect(() => {
    //   console.log(refreshPokemon)
    //   console.log("true")
  }, [])

  return (
    <div className="app-container">
        <div className="pokemon-container">
            <div className="all-container">
                {/* {refreshPokemon && (() => getAllPokemons())} */}
                <button onClick={() => getAllPokemons()}>ede</button>
                {allPokemon.map((pokemon, index) => 
                    <PokemonMylist 
                        id={index}
                        image={pokemon.image}
                        name={pokemon.name}
                        type={pokemon.type}
                        nickname = {pokemon.nickame}
                        setRefresh = {setRefreshPokemon}
                        key={index}
                    />
                )}
            </div>
        </div>
    </div>
  );
};

export default MyList;